import { ArrowDropDown, Notifications, Search } from "@material-ui/icons";
import { useState } from "react";
import "./navbar.scss";
import netflixLogo from '../../pages/Netflix_Logo_PMS.png';
import { Link } from 'react-router-dom';

const Navbar = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  window.onscroll = () => {
    setIsScrolled(window.pageYOffset === 0 ? false : true)
    return () => (window.onscroll = null);
  }
  // console.log(isScrolled);
  return (
    // <div classNameName={isScrolled ? "navbar scrolled" : "navbar"}>
    //   <div classNameName="container">
    //     <div classNameName="left">
    //       <img
    //         src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/2560px-Netflix_2015_logo.svg.png"
    //         alt=""
    //       />
    //       <span>HomePage</span>
    //       <span>Series</span>
    //       <span>Movies</span>
    //       <span>New Popular</span>
    //       <span>My List</span>
    //     </div>
    //     <div classNameName="right">
    //       <Search classNameName="icon" />
    //       <span>KID</span>
    //       <Notifications classNameName="icon" />
    //       <img
    //         src="https://images.pexels.com/photos/6899260/pexels-photo-6899260.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500"
    //         alt=""
    //       />
    //       <div classNameName="profile">
    //         <ArrowDropDown classNameName="icon" />
    //         <div classNameName="options">
    //             <span>Settings</span>
    //             <span>Logout</span>
    //         </div>
    //       </div>
    //     </div>
    //   </div>
    // </div>

    <nav className={`navbar navbar-expand-lg ${isScrolled ? 'color1' : 'color2'}`}>
      <div className="container">
        <Link className="navbar-brand" to=''>
          <img
            src={netflixLogo}
            alt=""
          />
        </Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse d-flex justify-content-center" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link to='' className="nav-link active" aria-current="page">Home</Link>
            </li>
            <li className="nav-item">
              <Link to='' className="nav-link">Series</Link>
            </li>
            <li className="nav-item">
              <Link to='' className="nav-link">Movies</Link>
            </li>
            <li className="nav-item">
              <Link to='' className="nav-link">New Popular</Link>
            </li>
            <li className="nav-item">
              <Link to='' className="nav-link">My List</Link>
            </li>
          </ul>
          {/* <div className="d-flex">
            <Link className="btn btn-outline-light me-1" to='/register'>Signup</Link>
            <Link className="btn btn-outline-light" to='/login'>Signin</Link>
          </div> */}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
